import html2pdf from "html-to-pdf-js";
import React from "react";
import { Style2 } from "./Style2";

const Resume = ({ close }) => {
  const refContainer = React.useRef(null);

  const handlePDF = () => {
    var element = refContainer.current;
    var opt = {
      pagebreak: { mode: "avoid-all" },
      margin: 0,
      filename: "CV_Andres_Movilla.pdf",
      image: { type: "jpeg", quality: 0.98 },
      html2canvas: { scale: 1 },
      jsPDF: { unit: "in", format: "letter", orientation: "portrait" },
    };
    html2pdf().set(opt).from(element).save();
  };

  return (
    <>
      <Style2 refProps={refContainer} />
      <div className="resume__control-buttons">
        <button className="resume__download" onClick={handlePDF}>
          <div className={`logo-div download-logo`} />
        </button>
        <button className="resume__close" onClick={close}>
          <div className={`logo-div close-logo`} />
        </button>
      </div>
    </>
  );
};

export { Resume };
