import React from 'react'

const Title = ({children}) => {
    const [displayedString, setDisplayedString] = React.useState(0)
    const [scrambledText, setScrambledText] = React.useState(shuffled(children))

    React.useEffect(() => {
        const handleAnim = () => {
            setDisplayedString(previous => {
                if (previous > scrambledText.length * 6) {
                    return 0
                } else {
                    return previous + 1
                }
            })
        }

        const interval = setInterval(handleAnim, 100)

        return () => clearInterval(interval)
    }, [])

    return <div className={'segmented-title'}>
        <div className={`layer unscrambled ${displayedString >= scrambledText.length * 1.25 ? 'hidden' : ''}`}>
            {children}
        </div>
        {
            scrambledText.map((item, index) =>
                <div
                    key={index}
                    className={`layer scrambled ${displayedString >= scrambledText.length - index ? 'hidden' : ''}`}
                >
                    {item}
                </div>
            )
        }
    </div>
}

const shuffled = word => {
    const res = []
    res.push(word)
    for (let i = 0; i < word.length; i++) {
        res.push(res[i].substring(1) + res[i].charAt(0))
    }
    res.push(word)
    res.push(word)
    return res
}

export default Title