import React from "react";
import data from "./utils";
import {VibratingTitle} from "../Titles";

const Section = () =>
    <div className={'section-contact'}>
        <h2 className={'serif x-large'}>
            <strong><VibratingTitle>
                Let's talk
            </VibratingTitle></strong>
        </h2>
        <div className={'section-contact__contents'}>
            {
                data.map(
                    (item, i) => <a
                        className={'redirect-btn'}
                        href={item.link}
                        key={i}
                        target={'_blank'}
                        rel={'noopener noreferrer'}
                    >
                        <span className={'label'}>
                            {item.label}
                        </span>
                        <div className={`logo-div ${item.className}`}/>
                    </a>
                )
            }
        </div>
    </div>

export default Section