const Brick = ({
                   logo,
                   title,
                   description,
                   img,
                   brickHeight,
                   rotation
               }) => <div
    style={{"--brick-height": brickHeight, "--bkg-rotation": rotation}}
    className={'brick'}
>
    <div className={'brick__background'}>
        <img src={img}/>
    </div>

    <div className={'brick__foreground'}>
        <div>{title}</div>
    </div>
</div>

export default Brick