import React from 'react';

const Component = ({title, data, close}) => <div className={'glass-popover__backdrop'} onClick={close}>
    <div className={'glass-popover'}>
        <div className={'glass-popover__title serif big'}>
            {title}
        </div>
        <ul className={'glass-popover__contents'}>
            {
                data.map((item, index) => <Item key={index} item={item} />)
            }
        </ul>
    </div>
    <div className={'glass-popover__close text-gray'}>
        Anywhere to close
    </div>
</div>

const Item = ({item}) => <li style={{"--underline-width": item.value}} >
    <div style={{display: "flex", justifyContent: "space-between"}}>
        <span>{item.name}</span>
        <span style={{color: "var(--color-mid"}}>{item.value}%</span>
    </div>
    <div className={'glass-popover__underline'}>
        <div className={'glass-popover__underline-fill'}/>
    </div>
</li>

export default Component