import React from "react";
import { Header } from "./components/Header";
import { IntroductionSection } from "./components/Introduction";
import { ExperienceSection } from "./components/Experience";
import { SkillsSection } from "./components/Skills";
import { ProjectsSection } from "./components/Projects";
import { ContactSection } from "./components/Contact";
import { Footer } from "./components/Footer";

const App = () => {
  const [mousePosition, setMousePosition] = React.useState({ x: 1, y: 1 });
  const [elementSize, setElementSize] = React.useState({ width: 1, height: 1 });

  const handleClick = (e) => {
    const { innerWidth: width, innerHeight: height } = e.view;
    const { clientX: x, clientY: y } = e;
    setMousePosition({ x, y });
    setElementSize({ width, height });
  };

  const displacement = {
    "--displacement-x":
      (mousePosition.x - elementSize.width * 0.5) / elementSize.width,
    "--displacement-y":
      (mousePosition.y - elementSize.height * 0.5) / elementSize.height,
  };

  React.useEffect(() => {
    let root = document.getElementById("root");

    root.addEventListener("click", handleClick);
  }, []);

  return (
    <>
      <Header />
      <div style={displacement}>
        <IntroductionSection />
        <ExperienceSection />
        <SkillsSection />
        <ProjectsSection />
        <ContactSection />
      </div>
      <Footer />
    </>
  );
};

export default App;
