import React from 'react'

const Title = ({children}) => {
    return <div className={'glitched-title'}>
        <div className={'red layer'}>
            {children}
        </div>
        <div className={'green layer'}>
            {children}
        </div>
        <div className={'blue layer'}>
            {children}
        </div>
    </div>
}

export default Title