import {
  education_history,
  professional_history,
  recognition_history,
  prog_languages,
  comp_skills,
  languages,
} from "./utils";

const Section = ({ title, children }) => (
  <>
    <div className="resume__section">
      <div className="resume__section__title">
        <h2>{title}</h2>
      </div>
      <div className="resume__section__contents">
        <hr />
        {children}
      </div>
    </div>
  </>
);

const HistoryItem = ({ title, subtitle, location, timestamp, details }) => {
  return (
    <div className="resume__history-item">
      <div className="resume__history-item__title">
        <div>{timestamp}</div>
        <h3>{title}</h3>
      </div>
      <div className="resume__history-item__contents">
        <div>{location}</div>
        <div>
          <span>{subtitle}</span>
          <ul>
            {details?.map((item, key) => (
              <li key={key}>{item}</li>
            ))}
          </ul>
        </div>
      </div>
    </div>
  );
};

export const Style2 = ({ refProps }) => (
  <div
    className="serif resume__style2"
    style={{
      backgroundColor: "rgb(var(--color-background))",
    }}
    ref={refProps}
  >
    <div className="resume__header">
      <div className="resume__header__part">
        <img src={"assets/pic2.png"} alt={"Introduction Picture"} />
      </div>
      <div className="resume__header__part">
        <h2>Andrés F. Movilla Obregón</h2>
        <span>Mobile: +57 (301) 353-8557</span>
        <span>E-mail: amovilla1103@gmail.com</span>
        <span>Portfolio: https://andremov.dev/</span>
      </div>
    </div>
    <div className="resume__contents">
      <Section title={"Computer Skills"}>
        {prog_languages.map(({ name, yrs }, key) => (
          <div key={key}>{`${name.join(", ")} - ${yrs} years`}</div>
        ))}
      </Section>

      <Section title={"Other Computer Skills"}>
        {comp_skills.map(({ name, yrs }, key) => (
          <div key={key}>{`${name} - ${yrs} years`}</div>
        ))}
      </Section>

      <Section title={"Work Experience"}>
        {professional_history.map((item, key) => (
          <HistoryItem key={key} {...item} />
        ))}
      </Section>

      <Section title={"Education"}>
        {education_history.map((item, key) => (
          <HistoryItem key={key} {...item} />
        ))}
      </Section>

      <Section title={"Recognitions"}>
        {recognition_history.map((item, key) => (
          <HistoryItem key={key} {...item} />
        ))}
      </Section>

      <Section title={"Languages"}>
        {languages.map(({ name, proficiency }, key) => (
          <div key={key}>{`${name} - ${proficiency}`}</div>
        ))}
      </Section>
    </div>
  </div>
);
