import {Masonry} from "../Masonry";
import {workProjects, gameProjects} from "./utils";
import {SegmentedTitle} from "../Titles";

const Section = () => <div className={'section-projects'}>
    <h2 className={'x-large serif'}>
        <strong><SegmentedTitle>
            Projects
        </SegmentedTitle></strong>
    </h2>
    <Masonry data={[...workProjects, ...gameProjects]} />
</div>

export default Section