const Title = ({children}) => <div className={'split-title'}>
        <div>
            {children}
        </div>
        <div
            style={{
                "--pos-left-1": '0%',
                "--pos-right-1": '26%',
                "--pos-left-2": '25%',
                "--pos-right-2": '35%',
                "--pos-left-3": '80%',
                "--pos-right-3": '95%',
            }}
            className={'split-title__cutout'}
        >
            {children}
        </div>
        <div
            style={{
                "--pos-left-1": '75%',
                "--pos-right-1": '100%',
                "--pos-left-2": '55%',
                "--pos-right-2": '60%',
                "--pos-left-3": '30%',
                "--pos-right-3": '45%',
            }}
            className={'split-title__cutout'}
        >
            {children}
        </div>
        <div
            style={{
                "--pos-left-1": '50%',
                "--pos-right-1": '76%',
                "--pos-left-2": '0%',
                "--pos-right-2": '15%',
                "--pos-left-3": '55%',
                "--pos-right-3": '65%',
            }}
            className={'split-title__cutout'}
        >
            {children}
        </div>
        <div
            style={{
                "--pos-left-1": '25%',
                "--pos-right-1": '51%',
                "--pos-left-2": '70%',
                "--pos-right-2": '80%',
                "--pos-left-3": '0%',
                "--pos-right-3": '20%',
            }}
            className={'split-title__cutout'}
        >
            {children}
        </div>
    </div>


export default Title