import React from 'react';

const Component = ({style, title, circleData, onClick}) => <div
    style={style}
    className={'glass-card'}
    onClick={onClick}
>
    <div className={'glass-card__background'}>
        {
            circleData.map(
                (item, key) => <div
                    key={key}
                    className={`circle ${item.colorClass}`}
                    style={item.style}
                />
            )
        }
    </div>
    <div className={'glass-card__foreground serif'}>
        <span>{title}</span>
        <img width="20" height="20" src={`/assets/arrow-right.svg`} />
    </div>
</div>


export default Component