import React from "react";
import { GlitchTitle } from "../Titles";
import Resume from "../Resume";
import langData from "./lang";
import contactData from "./contact";

const Section = () => {
  const [lang, setLang] = React.useState("EN");
  const [resumeOpen, setResumeOpen] = React.useState(false);
  const handleSwapGreeting = () => setLang(lang === "EN" ? "ES" : "EN");

  const handleResume = () => setResumeOpen(!resumeOpen);

  return (
    <>
      {resumeOpen && <Resume close={handleResume} />}
      <div className={"section-introduction"} onClick={handleSwapGreeting}>
        <div className={"greeting"}>
          <h2 className={"serif xx-large"}>
            <strong>
              <GlitchTitle>{langData.greeting[lang]}</GlitchTitle>
            </strong>
          </h2>
          <h3 className={"serif large"}>
            <span>{langData.intro_1[lang]}</span>{" "}
            <span className={"text-gray"}>{langData.intro_2[lang]}</span>
          </h3>
          <div className={"hide-phone hide-tablet description"}>
            <span className={"italics text-gray"}>
              {langData.description[lang]}
            </span>
            <div className={"contact-elements"}>
              {contactData.map((item, i) => (
                <a
                  className={"redirect-btn"}
                  href={item.link}
                  key={i}
                  target={"_blank"}
                  rel={"noopener noreferrer"}
                >
                  <div className={`logo-div ${item.className}`} />
                  <div className={"contact-tooltip"}>{item.label}</div>
                </a>
              ))}
              <div className={"redirect-btn"} onClick={handleResume}>
                <div className={`logo-div download-logo`} />
                <div className={"contact-tooltip"}>Resumé</div>
              </div>
            </div>
          </div>
        </div>
        <div className={"picture"}>
          <img src={"/assets/pic2.png"} alt={"Introduction Picture"} />
        </div>
        <div className={"hide-pc description"}>
          <span className={"italics text-gray"}>
            {langData.description[lang]}
          </span>
          <div className={"contact-elements"}>
            {contactData.map((item, i) => (
              <a
                className={"redirect-btn"}
                href={item.link}
                key={i}
                target={"_blank"}
                rel={"noopener noreferrer"}
              >
                <div className={`logo-div ${item.className}`} />
                <div className={"contact-tooltip"}>{item.label}</div>
              </a>
            ))}
            <div className={"redirect-btn"} onClick={handleResume}>
              <div className={`logo-div download-logo`} />
              <div className={"contact-tooltip"}>Resumé</div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
export default Section;
