import React from 'react';
import {SplitTitle} from "../Titles";
import {GlassCard} from "../GlassCard";
import data from "./utils";
import {GlassPopOver} from "../GlassPopOver";

const Section = () => {
    const [popover, openPopover] = React.useState(undefined)

    return <div className={'section-skills'}>
        <h2 className={'x-large serif'}>
            <strong><SplitTitle>
                Skills
            </SplitTitle></strong>
        </h2>
        {
            data.map(
                (item, key) =>
                    <GlassCard
                        style={{"--displacement": item.displacement}}
                        key={key}
                        title={item.title}
                        circleData={item.circleData}
                        onClick={() => openPopover(key+1)}
                    />
            )
        }
        {
            popover && <GlassPopOver
                title={data[popover-1].title}
                data={data[popover-1].popoverData}
                close={() => openPopover(undefined)}
            />
        }

    </div>
}
export default Section