import {TabbedMenu} from "../TabbedMenu";
import ExperienceData from './utils'
import {RainbowFillTitle} from "../Titles";

const Section = () => <div className={'section-experience'}>
	<h2 className={'serif'}>
		<strong><RainbowFillTitle>
			Experience
		</RainbowFillTitle></strong>
	</h2>
	<TabbedMenu
		data={ExperienceData}
	/>
</div>

export default Section