const Section = ({data}) =>
    <ul className={'tabbed-menu__tab-body'}>
        <hr/>
        {
            data.map((item, index) => <Item key={index} item={item} index={index + 1}/>)
        }
        <hr/>
    </ul>

const Item = ({item, index}) => <li className={'tabbed-menu__list-item'}>
    <div className={'tabbed-menu__list-index text-gray'}>
        {`0${index}`}
    </div>
    <strong>{item.title}</strong>
    <span>{item.subtitle}</span>
    <span className={'text-gray'}>{item.timestamp}</span>
</li>

export default Section