import React from 'react';

const TabMenu = ({tabs, selectTab, selectedTab}) => {
    const [hovering, setHovering] = React.useState(undefined)

    const handleHover = (index) => {
        if (hovering === index) {
            setHovering(undefined)
        } else {
            setHovering(index)
        }
    }

    return <div
        className={'tabbed-menu__tabs'}
        onMouseLeave={() => handleHover(hovering)}
    >
        {
            tabs.map((item, index) => <div
                onMouseEnter={() => handleHover(index)}
                className={'tabbed-menu__tab'}
                onClick={() => selectTab(index)}
                key={index}
            >
                {item}
            </div>)
        }
        <div
            style={{'--selected-item': hovering ?? selectedTab}}
            className={'tabbed-menu__tab-selector'}
        />
    </div>
}

export default TabMenu