import Brick from "./Brick";

const Component = ({data}) => <div className={'masonry'}>
    {
        data
            .map(item => item.group)
            .filter((item, index) => data.findIndex(s => s.group === item) === index)
            .map(group => <div key={group} className={'brick-group'}>
                {
                    data
                        .filter(item => item.group === group )
                        .map((brick, index) => <Brick key={index} {...brick} />)
                }
            </div>)
    }
</div>

export default Component